<template>
	<view>
		<view>
			<map style="width: 100%; height: 200px;" :latitude="latitude" :longitude="longitude" :markers="markers" @tap="tap" :include-points="markers"/>
		</view>
		<view>
				
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				id:0, 	//使用 marker点击事件 需要填写id
				latitude:null,
				longitude:null,
				markers:null,
				androidKey:'434414855eda40228059e3c5754ccf07'
			}
		},
		onLoad() {
			//获取当前位置
			this.getCurrLocation()
		},
		methods: {
			getCurrLocation(){
				uni.getLocation({
					type:'gcj02',
					geocode:true,
					success: (res) => {
						this.latitude = res.latitude;
						this.longitude = res.longitude;
						this.markers = [{
											id:1,
											latitude: res.latitude,
											longitude: res.longitude,
											iconPath: '../../../static/local.png',
											height:20,
											width:20
										}]
						// uni.openLocation({
						// 	latitude:res.latitude,
						// 	longitude:res.longitude,
						// 	success: (resp) => {
						// 		console.log("当前位置",resp)
						// 	}
						// })
						// var url="https://restapi.amap.com/v3/assistant/inputtips?parameters"
						//     data.key="你申请的key"
						//     return request.request({
						//         url: url,
						//         method: 'GET',
						//         data: data,
						//     })
						uni.request({
							url:'https://restapi.amap.com/v3/assistant/inputtips?parameters',
							method:'GET',
							data:{
								location:res.longitude+","+res.latitude,
								key:'434414855eda40228059e3c5754ccf07'
							},
							success: (res) => {
								console.log("res",res.data)
							}
						})
						console.log("当前经纬坐标：",res)
					}
				})
			},
			
			tap(e){
				console.log(e);
				var location = e.detail.longitude +','+e.detail.latitude
				myAmapFun.getRegeo({
				  location: location,
				  success: (data)=>{
					console.log("获取指定定位信息",data);
					this.selectAddr = {
						address:data[0].regeocodeData.formatted_address,
						latitude:e.detail.latitude,
						longitude:e.detail.longitude
					};
					this.markers[0].latitude = data[0].latitude;
					this.markers[0].longitude = data[0].longitude;
					myAmapFun.getPoiAround({
					  location: data[0].longitude+','+data[0].latitude,
					  success: (data)=>{
						console.log("获取当前的列表",data);
						this.dataTips = data.poisData;
					  },
					  fail: (info)=>{
						console.log(info)
					  }
					})
				  },
				  fail: (info)=>{
					console.log(info);
				  }
				})
			},
		}
	}
</script>

<style>
	.content {
		text-align: center;
		height: 400upx;
		margin-top: 200upx;
	}
</style>
